import React, { useState, useEffect } from 'react';

import {
  Card,
  CardBody,
  Col,
  Row,
  CardTitle,
  Form,
  Container,
  Label,
  Input,
  FormGroup,
  Button,
  Alert,
} from 'reactstrap';

//Import Breadcrumb
import Breadcrumbs from '../../components/Common/Breadcrumb';
import HorizontalLayout from '../../components/HorizontalLayout';
import { Link } from "react-router-dom"
import axios from 'axios';
import PropTypes from 'prop-types';
import LoadingSpinner from '../../components/LoadingSpinner';
import { ToastContainer} from "react-toastr";
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'


function AddCategory(props) {

  const headers = {
          'Authorization' : "Bearer "+localStorage.getItem('token')

        }

  const [loading, setLoading] = useState(false);
  const [loading1, setLoading1] = useState(false);
  const [data, setData] = useState([]);
  const [category, setcategory] = useState({ category_name: '', parent_category_id: ''});
useEffect(() => {
          {setLoading1(true)};
          axios.get(`${process.env.REACT_APP_BASE_APIURL}listCategory?is_dropdown=1`,{headers})
            .then(response => {
                     setData(response.data.data);
                     {setLoading1(false)}
               })
              .catch((error) => {
                  toastr.error(error.response.data.message);

                   {setLoading1(false)}
              })
        }, []);

const InsertCategory = (e)=>{
         e.preventDefault();

        {setLoading(true)};
        const data = { category_name:category.category_name, parent_category_id: category.parent_category_id};
         axios.post( `${process.env.REACT_APP_BASE_APIURL}addCategory`, data, {headers} )

                .then(response => {
                    if(response.data.success == true){
                        props.history.push('/category');
                        toastr.success(response.data.message);
                        {setLoading(false)};
                    }else{
                        props.history.push('/add-category');
                        toastr.error(response.data.message);
                        {setLoading(false)};
                    }
                })
                .catch((error) => {
                 {setLoading(false)};
                 toastr.error(error.response.data.message);
                })

      }


const ResetCategory = () => {
  document.getElementById("AddCategory").reset();
}

  const onChange = (e) => {
    e.persist();
    setcategory({...category, [e.target.name]: e.target.value});
  }

return(
 <React.Fragment>
      <HorizontalLayout/>

      <div className="page-content">
        <Container fluid={true}>
        <Form onSubmit={InsertCategory} method="POST" id="AddCategory">

        <div className="page-title-box d-flex align-items-center justify-content-between">

            <div className="page-title">
                <ol className="breadcrumb m-0">
                    <li className="breadcrumb-item"><a href="#!">Home</a></li>
                    <li className="breadcrumb-item">Masters</li>
                    <li className="breadcrumb-item"><Link to="/category">Category</Link></li>
                    <li className="breadcrumb-item active">Add Category</li>
                </ol>
            </div>

            <div className="page-title-right">
                <ol className="breadcrumb m-0">
                    <li><Link to="/category" className="btn btn-primary btn-sm"><i className="fa fa-chevron-right">&nbsp;Back</i></Link></li>&nbsp;
                    <li><button onClick = {ResetCategory} type="reset" className="btn btn-primary btn-sm"><i className="fa fa-reply">&nbsp;Reset</i></button></li>
                    &nbsp;
                    { loading ? <center><LoadingSpinner /></center> :<li>
                       <button type="submit" className="btn btn-primary btn-sm"><i className="fa fa-check">&nbsp;Submit</i></button>
                    </li>}
                </ol>
            </div>
        </div>
                          <div className="row">
                            <div className="col-12">
                                <div className="card">
                                    <div className="card-body">

                                        <h5 className="alert alert-success"><i className="fa fa-comment">&nbsp;Basic Info</i></h5>

                                         <div className="mb-3 row">
                                            <div className="form-group">
                                                <div className="row">

                                                     <div className="col-md-6">
                                                        <label className="required-field">Name</label>
                                                        <input type="text" id="category_name" name="category_name" className="form-control" placeholder="Enter Category Name" onChange={ onChange }  required/>
                                                    </div>

                                                     <div className="col-md-6">
                                                        <label>Category Parent</label>

                                                         {loading1 ? <LoadingSpinner /> :  <select className="form-select" id="parent_category_id" name="parent_category_id" onChange={ onChange } >
                                                             <option value="">Select Category</option>
                                                            { data.map((option, key) => <option value={option.id} key={key} >{option.category_name}</option>) }

                                                         </select> }
                                                    </div>

                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
      </Form>
        </Container>
      </div>
    </React.Fragment>
  )
}

export default AddCategory
